	module.exports = {"pathContext":{"group":[{"node":{"id":"/Users/CharlieMacbook/code/charliemchapman.com/src/posts/2018/09/02/first_post.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"First Post","date":"02 September, 2018"},"fields":{"slug":"/posts/2018/09/02/first_post/"},"html":"<p>It's been a while since I wrote a blog.  A quick look at my old Blogger account shows my last post was August 5, 2012.  That was 2 jobs, 2 kids, and 1 house ago!</p>\n<p>I'm not sure why I stopped, but over the last couple of months I've found myself wanting to write again. Unfortunately the task of getting a blog up an running again has always stopped me.  Last week I finally decided to jump in and build a new site.</p>\n<p>I had really good experience with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> building the website for my podcast <a href=\"https://atownfm.com/\">A-Town FM</a> so I decided to use it again, and boy I'm glad I did.  I've had so much fun putting the site together over the last week that I've completely forgotten what the article I wanted to write was that sparked me to build this in the first place.</p>\n<p>For some reason I've always had an easy time starting projects, and putting the time in to truly build them out.  But when it comes time to actually release them to the world, the need to get it <em>just right</em> freezes me up to the point that I often don't release at all.</p>\n<p>I'm hoping by releasing this post with this (probably buggy<sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup>) version of the site to the world, I'll get over that hump and just enjoy adding to it over time.</p>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>Seriously, I haven't even opened the page on a Windows computer, much less Edge.  So something is bound to be broken.</p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>"}}],"pathPrefix":"","first":false,"last":true,"index":3,"pageCount":3,"additionalContext":{}}}

